@use "./../abstract/colors"as c;

.card__plan {
 padding: 1.5rem 0;
 display: flex;
 align-items: center;
 justify-content: center;
 position: relative;
 color: c.$Grayish-Blue;
 font-size: .8rem;

 &::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0.1rem;
  background-color: c.$Dark-Desaturated-Blue;
  opacity: 0.2;
 }

 .card__plan--button {
  height: 1.5rem;
  margin: 0 1rem;
  width: 2.5rem;
  border: none;
  border-radius: 1rem;
  // background-color: c.;
  cursor: pointer;
  position: relative;

  span {
   background-color: c.$White;
   border-radius: 50%;
   height: 1rem;
   right: 4px;
   position: absolute;
   top: 0;
   width: 1rem;
   z-index: 10;
   transform: translate(0, 25%);
   transition: .3s;
  }

  .active--toggle {
   left: 4px;
  }
 }

 .active--btn {
  background-color: c.$Soft-Cyan;
 }

 .card__plan__yaerly {
  position: relative;

  &::after {
   content: "25% discount";
   padding: 0 0.3rem;
   height: 100%;
   display: flex;
   align-items: center;
   justify-content: center;
   position: absolute;
   top: 0;
   right: -5rem;
   background-color: c.$Light-Grayish-Red;
   color: c.$Light-Red;
   font-size: .6rem;
   border-radius: 1rem;
  }
 }
}